//Burger

const burgerBtn = document.querySelector(".header__burger");
const navBtns = document.querySelectorAll(".header__nav-text");
const nav = document.querySelector(".header__nav");

navBtns.forEach(item => {
	item.addEventListener("click", (e) => {
		if (burgerBtn.classList.contains("active")) {
			nav.classList.toggle("open");
			burgerBtn.classList.toggle("active");
			document.querySelector("body").style.overflow = "";
		}
	})
})

burgerBtn.addEventListener("click", e => {
	e.preventDefault()
	burgerBtn.classList.toggle("active")
	burgerBtn.classList.contains("active")
    ? (document.querySelector("body").style.overflow = "hidden")
    : (document.querySelector("body").style.overflow = "");
	nav.classList.toggle("open")
})

//Slider

const items = document.querySelector(".slider__body-items");
const currentEl = document.querySelector(".slider__controller-counter-current");
const outOfEl = document.querySelector(".slider__controller-counter-outof")
const toLeft = document.querySelector(".slider__controller-arrow_left");
const toRight = document.querySelector(".slider__controller-arrow_right");

const amountOfItems = document.querySelectorAll(".slider__body-items-item").length;
outOfEl.innerHTML = `/${amountOfItems}`;

let current = 1
const $PAGE_WIDTH = "120"

const toRightHandle = () => {
	current === amountOfItems ? current = 1 : current++;
	currentEl.innerHTML = current;
	items.style.transform = `translateX(${(current - 1) * -$PAGE_WIDTH}%)`
}

toRight.addEventListener("click", toRightHandle);


const toLeftHandle = () => {
	current === 1 ? current = amountOfItems : current--;
	currentEl.innerHTML = current;
  items.style.transform = `translateX(${(current - 1) * -$PAGE_WIDTH}%)`;
};

toLeft.addEventListener("click", toLeftHandle)

